* {
    box-sizing: border-box;
    margin: 0;
}

header {
    padding: 30px 0 30px 0;
    background: #343a40;
    color: #fff;
    text-align: center;
}

header h1 {
    display: inline;
    margin-left: 15px;
    font-weight: 300;
}

input:focus, 
select:focus
{
    outline: none;
}

.input {
    width: 100px;
    padding: 20px 0 20px 0;
    margin: 0 auto;
}

.search-area form {
    width: 500px;
    margin: 0 auto;
    padding: 20px 0px 20px 140px;
}

.search-area button {
    display: inline;
    background: #343a40;
    color: #fff;
    border: none;
    padding: 8px 13px 10px 13px;
    margin: 0 15px 0 0;
    position: relative;
    top: -2px;
}

input {
    width: 150px;
    padding: 5px;
    font-size: 1em;
}

.List {
    display: grid;
    margin: 20px 0 50px 0;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 1fr;
    grid-gap: 1em;
}

.card-container {
    height: 450px;
    width: 275px;
    border: 1px solid rgba(0,0,0,.125);
    margin: 0 auto;
    border-radius: 5px;
    overflow: hidden;
}

.card-container Button {
    display: inline;
    border-radius: 5px;
    padding: 8px 13px 10px 13px;
    margin: 5px 15px 0 0;
    position: relative;
    top: -2px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.desc {
    height: 130px;
    padding: 10px;
}

.button {
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  border: solid 1px #1A4575;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.4), 0 1px 1px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.4), 0 1px 1px rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.4), 0 1px 1px rgba(0, 0, 0, 0.2);
  background: #3A68A1;
  color: #fee1cc;
  text-decoration: none;
  padding: 8px;
  text-decoration: none;
  display: inline;
  position:relative; 
  top:20px;
}

.desc h2 {
    font-size: 1em;    
    font-weight: 400;
}
.desc h3, p {
    font-weight: 300;
}

.desc h3 {
    color: #6c757d;
    font-size: 1em;
    padding: 10px 0 10px 0;
}

.desc p {
    color: #6c757d;
}

img {
    width: 100%;
    height: 250px;
}

select {
    background-color: #343a40;
    color: #fff;
    height: 32px;
    width: 70px;
    position: relative;
    top: -2px;
}